import React, { useState, useRef, useEffect, useCallback } from "react";
import classNames from "classnames";
import moment, { Moment, isMoment } from "moment";
import CalendarPart from "../calendar/CalendarPart";
import { BubbleContent } from "../bubble";
import { Overlay } from "../overlay";
import { useOutsideClick } from "../_util/use-outside-click";
import { useDefaultValue } from "../form/controlled";
import { Input } from "../input";
import { TimeTable } from "./TimeTable";
import { Button } from "../button";
import { useTranslation } from "../i18n";
import { DropdownBox } from "../dropdown";
import { TimePickerProps } from "./TimeProps";
import {
  getHourMinuteSecond,
  genShowHourMinuteSecond,
  getDisabledHours,
  getDisabledSeconds,
  getDisabledMinutes,
} from "./util";
import { withStatics } from "../_util/with-statics";
import { TimeRangePicker } from "./TimeRangePicker";

const Keys = {
  "13": "enter",
  "27": "esc",
};

export const TimePicker = withStatics(
  function TimePicker(props: TimePickerProps) {
    const t = useTranslation(moment);

    const {
      value,
      onChange,
      format = "HH:mm:ss",
      placeholder = t.selectTime,
      disabled = false,
      hourStep = 1,
      minuteStep = 1,
      secondStep = 1,
      onOpenChange = () => null,
      className,
      style,
    } = useDefaultValue(props);

    const [curValue, setCurValue] = useState<Moment>(value);
    const [active, setActive] = useState<boolean>(false);
    const [bubble, setBubble] = useState<string | null>(null);

    const getInputValue = useCallback(
      value => {
        return isMoment(value) ? value.locale(t.locale).format(format) : "";
      },
      [t.locale, format]
    );

    const [inputValue, setInputValue] = useState<string>(
      getInputValue(curValue)
    );

    useEffect(() => {
      setCurValue(value);
      setInputValue(getInputValue(value));
    }, [format, getInputValue, value]);

    const inputRef = useRef<HTMLInputElement>(null);
    const selectRef = useRef<HTMLElement>(null);

    const { listen } = useOutsideClick([inputRef, selectRef]);
    listen(close);

    function handleInputChange(content: string) {
      setInputValue(content);

      const value = moment(content, format, true);

      // 格式校验
      if (!value.isValid()) {
        setBubble(t.invalidFormat(format));
        return;
      }

      const { hour, minute, second } = getHourMinuteSecond(value);
      const { showHour, showMinute, showSecond } = genShowHourMinuteSecond(
        format
      );

      // 范围校验
      if (
        (showHour && getDisabledHours(props).includes(hour)) ||
        (showMinute && getDisabledMinutes(hour, props).includes(minute)) ||
        (showSecond && getDisabledSeconds(hour, minute, props).includes(second))
      ) {
        setBubble(t.invalidTime);
        return;
      }

      // 步长校验
      if (
        (showHour && hour % hourStep) ||
        (showMinute && minute % minuteStep) ||
        (showSecond && second % secondStep)
      ) {
        setBubble(t.invalidTime);
        return;
      }

      setCurValue(value);
      setBubble(null);
    }

    function handleKeyDown(event: React.KeyboardEvent) {
      switch (Keys[event.keyCode]) {
        case "enter":
          if (!bubble) {
            handleOk(event);
          }
          break;
        case "esc":
          close();
          break;
      }
    }

    function handleSelectChange(value: Moment) {
      setBubble(null);
      setInputValue(getInputValue(value));
      setCurValue(value);
    }

    function handleOk(event: React.SyntheticEvent) {
      onChange(curValue, { event });
      close();
    }

    function open() {
      if (disabled) {
        return;
      }
      setActive(true);
      if (!active) {
        onOpenChange(true);
      }
    }

    function close() {
      setInputValue(getInputValue(value));
      setActive(false);
      setBubble(null);
      inputRef.current.blur();
      if (active) {
        onOpenChange(false);
      }
    }

    return (
      <div className={classNames("tea-timepicker", className)} style={style}>
        <Overlay
          layers={[
            <Overlay.Layer
              key="bubble"
              placement="top"
              visible={active && !!bubble}
              content={({ placement }) => (
                <BubbleContent placement={placement} error>
                  {bubble}
                </BubbleContent>
              )}
            />,
            <Overlay.Layer
              key="combobox"
              visible={active}
              overlayRef={e => {
                selectRef.current = e;
              }}
              content={
                <DropdownBox>
                  <CalendarPart.Panel timeMode>
                    <CalendarPart.Body>
                      <TimeTable
                        {...props}
                        value={curValue}
                        onChange={handleSelectChange}
                      />
                    </CalendarPart.Body>
                    <CalendarPart.Footer
                      right={
                        <Button type="primary" onClick={handleOk}>
                          确定
                        </Button>
                      }
                    />
                  </CalendarPart.Panel>
                </DropdownBox>
              }
            />,
          ]}
        >
          {ref => (
            <div className="tea-timepicker__input" ref={ref} onClick={open}>
              <Input
                ref={inputRef}
                maxLength={8}
                disabled={disabled}
                placeholder={placeholder}
                value={inputValue}
                onChange={handleInputChange}
                onKeyDown={handleKeyDown}
              />
            </div>
          )}
        </Overlay>
      </div>
    );
  },
  {
    defaultLabelAlign: "middle",
    RangePicker: TimeRangePicker,
  }
);
